.header__container {
  padding: 28px 27px 31px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (min-width: 400px) {
  .header__container {
    width: 100%;
  }
}

@media screen and (min-width: 1000px) {
  .header__container {
    padding: 45px 0 41px;
  }
}
