.header {
  box-sizing: border-box;
  max-width: 880px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  flex-direction: column-reverse;
}

@media (min-width: 400px) {
  .header {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
